@import url('https://fonts.googleapis.com/css2?family=Poppins&family=Russo+One&display=swap');
body{
    background: white;
    color : black;
    font-family: Poppins , "Russo One";
    overflow : hidden;
}

.mytodos .title{
    text-align: center;
    word-spacing: 10px;
    letter-spacing: 2px;
    font-size : 30px;
    text-decoration :line-through;
}


.todoform , .todolist{
    display: flex;
    justify-content: center;
    align-items: center;
}

.todoform button{
    position: absolute;
    right : 30%;
    outline: none;
    border : none;
    background: transparent;
    height : 30px;
    width: 30px;
    cursor: pointer;
}

.todoform span{
    font-size : 26px;
    color : orange;
    display: flex;
    align-items: center;
    justify-content: center;
}

.todoform .input{
    margin : 20px auto;
    padding : 5px 15px;
    font-family: Poppins;
    width : 39.7%;
    height : 35px;
    outline : none;
    border : none;
    border : 1px solid grey;
    font-size: 16px;
}



.todolist ul{ 
    width : 41.5%;
    padding: 0px;
    margin : 0px;
}
.todolist li{
    
    padding : 5px 15px;
    color: black;
    font-size : 18px;
    font-family : Poppins;
    list-style: none;
    margin-bottom: 3px;
    border-left : 3px solid orange;
    background: rgba(128, 128, 128,0.3);
}
.todolist li:last-child{
    margin-bottom: 0px;
}

.alert-msg{
    position: absolute;
    top : 5%;
    right : 1%;
    width : 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height : auto;
    width : 300px;
    padding : 2px 15px;
    animation: slide 0.35s ease-in forwards;
}
@keyframes slide{
    from{
        right : -10%;
    }
    to{
        right : 1%;
    }
}

.alert-msg::before{
    position: absolute;
    left : 0%;
    bottom: 0%;
    width : 0%;
    content : "";
    display: block;
    border-bottom : 1px solid grey;
    animation: border 2s forwards;
    animation-timing-function: linear;
}
@keyframes border{
    0%{
        width : 0%;
    }
    50%{
        width : 50%;
    }
    100%{
        width : 100%;
    }
}
.alert-msg p{
    font-size: 16px;
    font-weight: bold;
}
.alert-msg i{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    cursor: pointer;
}

@media screen and (max-width : 600px){
    .mytodos .title{
        text-align: center;
        word-spacing: 5px;
        letter-spacing: 1px;
        font-size : 20px;
    
    }
    .todoform .input{
        width : 62%;
        height : 30px;
        border : 1px solid grey;
        font-size: 14px;
    }
    .todolist ul{ 
        width : 70%;
        padding: 0px;
        margin : 0px;
        
    }
    .todolist li{
        font-size: 14px;
        
        padding : 5px;
    }
    .todoform button{
        position: absolute;
        right : 17%;
        height : 25px;
        width: 25px;
        border-radius: 0%;
        background: transparent;
        box-shadow: none;
    }
    .todoform span{
        position: relative;
        font-size : 20px;
        color : orange;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .alert-msg{
        position: absolute;
        top : 0%;
        left : 0%;
        width : 93%;
        height : auto;
        padding : 2px 15px;
        animation: none;
    }

    .alert-msg p{
        font-size: 14px;
    }
    .alert-msg i{
        font-size: 15px;
    }
}

